<template>
  <div>
    <breadcrumb v-bind:title="title"></breadcrumb>
    <div class="row">
      <div class="col-sm-12">
        <table class="table table-bordered">
          <tr>
            <th>نام سطح</th>
            <th>توضیحات سطح</th>
            <th>حذف</th>
          </tr>
          <tbody>
          <tr>
            <td>سطح یک</td>
            <td>توضیحات سطح یک</td>
            <td>
              <button style="margin-left: 10px;" class="btn btn-danger btn-sm">حذف</button>
              <button class="btn btn-primary btn-sm">ویرایش</button>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>

  </div>
</template>

<script>
  import Breadcrumb from '../../components/Breadcrumb'
  import Http from '../../services/HttpService/HttpService'
  import Notify from '../../services/Notifiction/Notification'

  export default {
    name: "ListLevels",
    components: {Breadcrumb},
    data() {
      return {
        title: "لیست سطح ها",
        levels: {}
      }
    },
    methods: {
      getLevels() {
        Http.get('https://jsonplaceholder.typicode.com/todos/1')
          .then(response => {
            this.levels = response.data;
            Notify.success("ok");
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    created() {
      this.getLevels();
    }
  }
</script>
<style scoped>
  table tr th {
    padding: 10px;
    background-color: #cccccc;
  }

</style>
