import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from './views/dashboard/Dashboard'
import ListUsers from './views/user/ListUsers'
import AddUser from './views/user/AddUser'
import ListLevels from './views/level/ListLevels'
import AddLevel from './views/level/AddLevel'
import Login from './views/login/Login'
import DashView from './Dash'

Vue.use(Router);

let router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [

    {
      path: '/login',
      name: 'Login',
      component: Login,
      meta: {
        guest: true
      }
    },

    {
      path: '/',
      component: DashView,
      meta: {
        requiresAuth: true
      },
      children: [

        {
          path: 'users/list',
          name: 'ListUsers',
          component: ListUsers,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'users/add',
          name: 'AddUser',
          component: AddUser,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'levels/list',
          name: 'ListLevels',
          component: ListLevels,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: 'levels/add',
          name: 'AddLevel',
          component: AddLevel,
          meta: {
            requiresAuth: true
          }
        },
        {
          path: '',
          name: 'Dashboard',
          component: Dashboard,
          meta: {
            requiresAuth: true
          }
        }


      ]
    }

  ]
});


router.beforeEach((to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    if (localStorage.getItem('jwt') == null) {
      next({
        path: '/login',
        params: {nextUrl: to.fullPath}
      })
    } else {
      let user = JSON.parse(localStorage.getItem('user'));
      if (user != null) {

        next()

      } else {
        next({
          path: '/login',
          params: {nextUrl: to.fullPath}
        })
      }
    }
  } else if (to.matched.some(record => record.meta.guest)) {
    if (localStorage.getItem('jwt') == null) {
      next()
    }
    else {
      next({name: 'Dashboard'})
    }
  } else {
    next()
  }
});


export default router
